	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.flickr.com/photos/papascott/349217396/\" title=\"Photo Sharing\"><img src=\"http://farm1.static.flickr.com/134/349217396_1486c517fd.jpg\" width=\"500\" height=\"375\" alt=\"IMG 1884\" /></a></p>\n<p>Meet Tinti, Netzi and Nepomuk, an octopus, spider, and dragon that Christopher freed from Ikea yesterday. His shopping was more successful than mine. The shelves I brought home were the wrong color (birch instead of beech).</p>\n<p>The box and the styrofoam peanuts were a late Christmas package from Arizona, and were more fun than any toy.</p>","fields":{"title":"The Three from Ikea","date":"07 Jan 2007","url":"https://papascott.de/2007-01-07-the-three-from-ikea/"}}},"pathContext":{"slug":"/2007-01-07-the-three-from-ikea/","prev":{"fields":{"title":"A one-liner worth 1000 words","date":"03 Jan 2007","slug":"/2007-01-03-a-one-liner-worth-1000-words/"}},"next":{"fields":{"title":"Needles and Pins","date":"08 Jan 2007","slug":"/2007-01-08-needles-and-pins/"}}}}

